import React, { Component } from 'react';
import land from '../assets/land.png';
import mine from '../assets/mine.png';
import corn from '../assets/corn.png';
import wheat from '../assets/wheat.png';
import tractor from '../assets/tractor.png';
import world from '../assets/world.png';
import './Farm.scss';

class Farm extends Component {
  constructor(props) {
    super(props);

    this.state = {
      allocateLand: 0,
      compostCornAmount: 0,
      isReleasing: false
    };

    this.buyLand = this.buyLand.bind(this);
    this.buyCorn = this.buyCorn.bind(this);
    this.handleAllocateLand = this.handleAllocateLand.bind(this);
    this.maxAllocateLand = this.maxAllocateLand.bind(this);
    this.allocateLand = this.allocateLand.bind(this);
    this.releaseLand = this.releaseLand.bind(this);
    this.cancelRelease = this.cancelRelease.bind(this);
    this.confirmRelease = this.confirmRelease.bind(this);
    this.harvestCorn = this.harvestCorn.bind(this);
    this.handleCompostCornAmount = this.handleCompostCornAmount.bind(this);
    this.maxCompostCornAmount = this.maxCompostCornAmount.bind(this);
    this.compostCorn = this.compostCorn.bind(this);
  }

  buyLand(e) {
    e.preventDefault();
    window.open('https://app.uniswap.org/#/swap?outputCurrency=' + this.props.contracts.landAddress, '_blank');
  }

  buyCorn(e) {
    e.preventDefault();
    window.open('https://app.uniswap.org/#/swap?outputCurrency=' + this.props.contracts.cornAddress, '_blank');
  }

  // Allocate Land
  handleAllocateLand(e) {
    this.setState({ allocateLand: e.target.value });
  }

  maxAllocateLand() {
    this.setState({ allocateLand: this.props.contracts.landBalance });
  }

  allocateLand() {
    this.props.ethereumservice.allocateLand(this.state.allocateLand);
  }

  // Release Land
  releaseLand() {
    this.setState({ isReleasing: true });
  }

  cancelRelease() {
    this.setState({ isReleasing: false });
  }

  confirmRelease() {
    this.props.ethereumservice.releaseLand();
  }

  // Harvest Corn
  harvestCorn() {
    this.props.ethereumservice.harvestCorn();
  }

  // Compost Corn
  handleCompostCornAmount(e) {
    this.setState({ compostCornAmount: e.target.value });
  }

  maxCompostCornAmount() {
    this.setState({ compostCornAmount: this.props.contracts.cornBalance });
  }

  compostCorn() {
    this.props.ethereumservice.compostCorn(this.state.compostCornAmount);
  }

  toFixed(amount, decimals) {
    return !amount || isNaN(amount) ? '' : Number.parseFloat(amount).toFixed(decimals);
  }

  render() {
    return (
      <section id="farm" className="is-vcentered">
        <div className="hero-body">
          <div className="container is-max-widescreen">
            {!this.props.contracts.isConnected && (
              <div className="tile is-parent is-center">
                <article
                  className="is-child box has-background-light is-button is-center"
                  onClick={() => this.props.ethereumservice.connectWallet()}>
                  <div className="content pt-4 px-3">
                    <h2 className="mb-0">Connect Wallet</h2>
                  </div>
                </article>
              </div>
            )}
            {this.props.contracts.isConnected && (
              <div className="tile is-ancestor">
                <div className="tile is-vertical is-12">
                  <div className="tile">
                    <div className="tile is-parent">
                      <article className="tile is-child box has-background-light">
                        <div className="title">
                          <div className="tabs is-fullwidth is-medium is-boxed">
                            <ul>
                              <li className="selected">
                                <a>
                                  <img alt="Farm" className="token" src={tractor} />
                                  <span className="tab-title">Farm</span>
                                </a>
                              </li>
                              <li className="disabled tooltip">
                                <span className="tooltip-text">Coming soon</span>
                                <a>
                                  <img alt="Mine" className="token" src={mine} />
                                  <span className="tab-title">Mine</span>
                                </a>
                              </li>
                            </ul>
                          </div>
                        </div>

                        <div className="content">{this.renderManageFarm()}</div>
                      </article>
                    </div>
                    <div className="tile is-parent">
                      <article className="tile is-child box has-background-light">
                        <div className="title">
                          <div className="tabs is-fullwidth is-medium is-boxed">
                            <ul>
                              <li className="selected">
                                <a>
                                  <img alt="Corn Token" className="token" src={corn} />
                                  <span className="tab-title">Corn</span>
                                </a>
                              </li>
                              <li className="disabled tooltip">
                                <span className="tooltip-text">Coming soon</span>
                                <a>
                                  <img alt="Wheat Token" className="token" src={wheat} />
                                  <span className="tab-title">Wheat</span>
                                </a>
                              </li>
                            </ul>
                          </div>
                        </div>
                        <div className="content">{this.renderTendCrops()}</div>
                      </article>
                    </div>
                  </div>
                  <div className="tile">
                    <div className="tile is-parent">
                      <article className="tile is-child box has-background-light">
                        <div className="title">
                          <div className="tabs is-fullwidth is-medium is-boxed">
                            <ul>
                              <li className="selected">
                                <a>
                                  <img alt="Your Statistics" className="token" src={land} />
                                  <span className="tab-title">Your Statistics</span>
                                </a>
                              </li>
                            </ul>
                          </div>
                        </div>
                        <div className="content">{this.renderFarmStats()}</div>
                      </article>
                    </div>
                    <div className="tile is-parent">
                      <article className="tile is-child box has-background-light">
                        <div className="title">
                          <div className="tabs is-fullwidth is-medium is-boxed">
                            <ul>
                              <li className="selected">
                                <a>
                                  <img alt="Global Statistics" className="token" src={world} />
                                  <span className="tab-title">Global Statistics</span>
                                </a>
                              </li>
                            </ul>
                          </div>
                        </div>
                        <div className="content">{this.renderGlobalStats()}</div>
                      </article>
                    </div>
                  </div>
                </div>
              </div>
            )}
          </div>
        </div>
      </section>
    );
  }

  renderManageFarm() {
    return (
      <div>
        {!this.props.contracts.isAuthorized && (
          <ul className="action-control">
            <li>
              <button className="button is-danger my-3" onClick={() => this.props.ethereumservice.approveContract()}>
                Grant planning permission
              </button>
            </li>
          </ul>
        )}

        <div className="info-control">
          {/* UNALLOCATED */}
          <h2 className="info">Unallocated :</h2>
          <h2 className="value">
            <span className="pre">{this.toFixed(this.props.contracts.landBalance, 2)} LAND</span>
            {this.props.contracts.landBalance > 0 && !this.props.prices.landUsd && (
              <span className="post">
                <button className="button is-white is-loading"></button>
              </span>
            )}
            {this.props.contracts.landBalance > 0 && this.props.prices.landUsd && (
              <span className="post">
                {' '}
                ({this.toFixed(this.props.prices.landUsd * this.props.contracts.landBalance, 2)} USD)
              </span>
            )}
          </h2>
        </div>

        <div className="info-control">
          {/* FARMLAND */}
          <h2 className="info">Farmland :</h2>
          <h2 className="value">
            <span className="pre">{this.toFixed(this.props.contracts.landInFarm, 2)} LAND</span>
            {this.props.contracts.landInFarm > 0 && !this.props.prices.landUsd && (
              <span className="post">
                <button className="button is-white is-loading"></button>
              </span>
            )}
            {this.props.contracts.landInFarm > 0 && this.props.prices.landUsd && (
              <span className="post">
                {' '}
                ({this.toFixed(this.props.prices.landUsd * this.props.contracts.landInFarm, 2)} USD)
              </span>
            )}
          </h2>
        </div>

        {!this.state.isReleasing && (
          <ul className="action-control">
            <li className="action-form">
              <div className="field has-addons">
                <div className="control">
                  <button
                    className="button is-success is-small"
                    onClick={this.maxAllocateLand}
                    disabled={!this.props.contracts.isAuthorized}>
                    Max{' '}
                  </button>
                </div>

                <div className="control input-control">
                  <input
                    className="input is-success is-small"
                    type="text"
                    placeholder="LAND Amount"
                    value={this.state.allocateLand}
                    onChange={this.handleAllocateLand}
                    disabled={!this.props.contracts.isAuthorized}
                  />
                </div>

                <div className="control">
                  <button
                    className="button is-success is-small"
                    onClick={this.allocateLand}
                    disabled={!this.props.contracts.isAuthorized}>
                    Allocate Land{' '}
                  </button>
                </div>
              </div>
            </li>
          </ul>
        )}

        {!this.state.isReleasing && (
          <ul className="action-control mt-5">
            <li>
              <button className="button is-land is-small" onClick={this.buyLand}>
                Buy Land{' '}
              </button>
            </li>

            <li>
              <button
                className="button is-dark is-small"
                onClick={this.releaseLand}
                disabled={!this.props.contracts.isAuthorized}>
                Release Land{' '}
              </button>
            </li>
          </ul>
        )}

        {this.state.isReleasing && (
          <ul className="action-control mt-3">
            <li className="has-text-danger has-text-weight-bold">
              Warning: Releasing land will cause the permanent loss of any growing corn and reset the farm maturity
              boost. Click cancel to harvest corn first.
            </li>
          </ul>
        )}

        {this.state.isReleasing && (
          <ul className="action-control mt-2">
            <li>
              <button className="button is-small" onClick={this.cancelRelease}>
                Cancel{' '}
              </button>
            </li>

            <li>
              <button className="button is-danger is-small" onClick={this.confirmRelease}>
                Confirm{' '}
              </button>
            </li>
          </ul>
        )}
      </div>
    );
  }

  renderTendCrops() {
    return (
      <div>
        <div className="info-control">
          <h2 className="info">Corn growing :</h2>
          <h2 className="value">
            <span className="pre">{this.toFixed(this.props.contracts.cornToHarvest, 8)} CORN</span>
            {this.props.contracts.cornToHarvest > 0 && !this.props.prices.cornUsd && (
              <span className="post">
                <button className="button is-white is-loading"></button>
              </span>
            )}
            {this.props.contracts.cornToHarvest > 0 && this.props.prices.cornUsd && (
              <span className="post">
                {' '}
                ({this.toFixed(this.props.prices.cornUsd * this.props.contracts.cornToHarvest, 2)} USD)
              </span>
            )}
          </h2>
        </div>

        <div className="info-control">
          {/* AVAILABLE */}
          <h2 className="info">Available corn :</h2>
          <h2 className="value">
            <span className="pre">{this.toFixed(this.props.contracts.cornBalance, 8)} CORN</span>
            {this.props.contracts.cornBalance > 0 && !this.props.prices.cornUsd && (
              <span className="post">
                <button className="button is-white is-loading"></button>
              </span>
            )}
            {this.props.contracts.cornBalance > 0 && this.props.prices.cornUsd && (
              <span className="post">
                {' '}
                ({this.toFixed(this.props.prices.cornUsd * this.props.contracts.cornBalance, 2)} USD)
              </span>
            )}
          </h2>
        </div>

        <ul className="action-control">
          <li>
            <button className="button is-warning is-small" onClick={this.buyCorn}>
              Buy Corn{' '}
            </button>
          </li>

          <li>
            <button
              className="button is-dark is-small"
              onClick={this.harvestCorn}
              disabled={!this.props.contracts.isAuthorized}>
              Harvest Corn{' '}
            </button>
          </li>
        </ul>

        <ul className="action-control mt-5">
          <li className="action-form">
            <div className="field has-addons">
              <div className="control">
                <button
                  className="button is-success is-small"
                  onClick={this.maxCompostCornAmount}
                  disabled={!this.props.contracts.isAuthorized}>
                  Max{' '}
                </button>
              </div>

              <div className="control input-control">
                <input
                  className="input is-success is-small"
                  type="text"
                  placeholder="CORN Amount"
                  value={this.state.compostCornAmount}
                  onChange={this.handleCompostCornAmount}
                  disabled={!this.props.contracts.isAuthorized}
                />
              </div>

              <div className="control">
                <button
                  className="button is-success is-small"
                  onClick={this.compostCorn}
                  disabled={!this.props.contracts.isAuthorized}>
                  Compost Corn
                </button>
              </div>
            </div>
          </li>
        </ul>
      </div>
    );
  }

  renderFarmStats() {
    return (
      <div>
        <div className="info-control">
          <h2 className="info">Growing season countdown : </h2>
          <h2 className="value">{this.props.contracts.growingCycleCountDown}</h2>
          {/* <progress className="progress" value={this.props.contracts.growingCycleCountDownSecs} max={this.props.contracts.endOfGrowingCycle}></progress> */}
        </div>

        <div className="info-control">
          <h2 className="info">Farm maturity boost : </h2>
          <h2 className="value ">x {this.toFixed(this.props.contracts.farmMaturityBoost, 4)}</h2>
          <progress
            className="progress is-success"
            value={this.props.contracts.farmMaturityBoost - 1}
            max="2"></progress>
        </div>

        <div className="info-control">
          <h2 className="info">Composting boost : </h2>
          <h2 className="value">x {this.toFixed(this.props.contracts.farmCompostBoost, 4)}</h2>
          <progress className="progress" value={this.props.contracts.farmCompostBoost - 1} max="9"></progress>
        </div>

        <div className="info-control">
          <h2 className="info">Corn composted : </h2>
          <h2 className="value">
            <span className="pre">{this.toFixed(this.props.contracts.cornComposted, 8)}</span>
            {typeof this.props.contracts.burnRequiredTo10x == 'number' &&
              typeof this.props.contracts.maxTo10x == 'number' && (
                <span className="post">({this.toFixed(this.props.contracts.maxTo10x, 2)} for x 10)</span>
              )}
          </h2>
          {typeof this.props.contracts.burnRequiredTo10x == 'number' && (
            <progress
              className="progress is-warning"
              value={this.props.contracts.cornComposted}
              max={this.props.contracts.maxTo10x}></progress>
          )}
          {typeof this.props.contracts.burnRequiredTo10x != 'number' && (
            <progress className="progress is-error" value={1} max={1}></progress>
          )}
        </div>
      </div>
    );
  }

  renderGlobalStats() {
    return (
      <div>
        <div className="info-control">
          <h2 className="info">Total Farms : </h2>
          <h2 className="value">{this.toFixed(this.props.contracts.globalNumberOfFarms, 0)}</h2>
        </div>
        <div className="info-control">
          <h2 className="info">Total Land in Farms : </h2>
          <h2 className="value">{this.toFixed(this.props.contracts.globalLandInFarm, 2)} LAND</h2>
        </div>
        <div className="info-control">
          <h2 className="info">Average Size of Farms : </h2>
          <h2 className="value">{this.toFixed(this.props.contracts.globalAverageLandInFarms)} LAND</h2>
        </div>
        <div className="info-control">
          <h2 className="info">Total Corn composted : </h2>
          <h2 className="value">{this.toFixed(this.props.contracts.globalComposted, 2)} CORN</h2>
        </div>
        <div className="info-control">
          <h2 className="info">Average Amount of Corn composted : </h2>
          <h2 className="value">{this.toFixed(this.props.contracts.globalAverageComposted, 2)} CORN</h2>
        </div>
      </div>
    );
  }
}

export default Farm;
