import { ethers } from 'ethers';
import claimContract from '../contracts/Claim.json';
import claimMerkle from '../contracts/ClaimMerkle.json';
import { Utils } from './Utils';

export class ClaimService {
  constructor() {
    this.reset();
  }

  reset() {
    this.claim = null;
    this.claimInfo = null;
  }

  loadClaimContract(networkConfig, signer) {
    this.claim = new ethers.Contract(networkConfig.claimContractAddress, claimContract.abi, signer);
  }

  checkClaim(walletAddress) {
    if (!this.claim) {
      this.claimInfo = {
        message: 'No Claim Available'
      };
      return new Promise((resolve, reject) => {});
    }

    var merkle = claimMerkle.claims[walletAddress];

    if (!merkle) {
      this.claimInfo = {
        message: 'No Claim Available'
      };
      return new Promise((resolve, reject) => {});
    }

    var amount = Number.parseInt(Utils.formatUnits(merkle.amount));

    return this.claim.isClaimed(merkle.index).then(claimed => {
      if (claimed) {
        this.claimInfo = {
          message: amount + ' LAND Claimed'
        };
      } else if (amount <= 0) {
        this.claimInfo = {
          message: 'No Claim Available'
        };
      } else {
        this.claimInfo = {
          merkleProof: merkle.proof,
          amount: amount,
          message: amount + ' LAND Available to Claim'
        };
      }
    });
  }

  sendClaim(walletAddress) {
    if (!this.claim) {
      return Promise.reject('Error: unable to load claim contract');
    }

    if (!this.claimInfo || this.claimInfo.amount <= 0) {
      return Promise.reject('Error: claim info shows nothing to claim');
    }

    var merkle = claimMerkle.claims[walletAddress];

    if (!merkle) {
      return Promise.reject('Error: address not found in merkle root');
    }

    return this.claim.claim(merkle.index, walletAddress, merkle.amount, merkle.proof).then(
      response => Promise.resolve(this.claimInfo.amount + ' LAND Claim Pending'),
      error => Promise.reject(error)
    );
  }
}
