import React, { Component } from 'react';
import logo from '../assets/logo.png';
import { Link } from 'react-router-dom';
import './NavBar.scss';

class NavBar extends Component {
  constructor(props) {
    super(props);

    this.state = {
      menuActiveClass: ''
    };

    this.handleBurgerClick = this.handleBurgerClick.bind(this);
    this.menuItemClick = this.menuItemClick.bind(this);
  }

  handleBurgerClick(e) {
    this.setState({
      menuActiveClass: this.state.menuActiveClass === '' ? 'is-active' : ''
    });
  }

  menuItemClick(e) {
    this.setState({
      menuActiveClass: ''
    });
  }

  toFixed(amount, decimals) {
    return !amount || isNaN(amount) ? '' : Number.parseFloat(amount).toFixed(decimals);
  }

  render() {
    return (
      <nav className="navbar is-transparent is-fixed-top" role="navigation" aria-label="main navigation">
        <div className="navbar-brand">
          <Link className="navbar-item" exact="true" to="/">
            <img className="logo" src={logo} width="45" />
            <span className="navbar-item logo-text">FARMLAND</span>
          </Link>
          <a
            onClick={this.handleBurgerClick}
            role="button"
            className={`navbar-burger burger ${this.state.menuActiveClass}`}
            aria-label="menu"
            aria-expanded="false"
            data-target="navbar-menu-items">
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </a>
        </div>

        <div
          id="navbar-menu-items"
          className={`navbar-menu ${this.state.menuActiveClass}`}
          onClick={this.menuItemClick}>
          <div className="navbar-start">
            <Link className="navbar-item" to="/farm">
              Farm
            </Link>
            <Link className="navbar-item" to="/docs">
              Help
            </Link>
          </div>

          <div className="navbar-end">
            <div className="navbar-item">
              <div className="buttons is-centered">
                <button className="button is-dark">
                  <span className="icon">
                    <i className="fas fa-gas-pump"></i>
                  </span>
                  <span>{this.toFixed(this.props.prices.gas, 0)}</span>
                </button>
                <a
                  className="button is-land"
                  target="_blank"
                  rel="noopener noreferrer"
                  href={'https://app.uniswap.org/#/swap?outputCurrency=' + this.props.contracts.landAddress}>
                  {' '}
                  {this.toFixed(this.props.contracts.landBalance, 2)} LAND{' '}
                </a>
                <button className="button is-danger">{this.toFixed(this.props.contracts.ethBalance, 2)} ETH</button>
                {!this.props.contracts.isConnected && (
                  <button className="button is-dark" onClick={() => this.props.ethereumservice.connectWallet()}>
                    Connect to a wallet
                  </button>
                )}
                {this.props.contracts.isConnected && (
                  <a
                    className="button is-info"
                    target="_blank"
                    rel="noopener noreferrer"
                    href={'https://etherscan.io/address/' + this.props.contracts.walletAddress}>
                    {this.props.contracts.walletAddress.substring(0, 6)}...
                    {this.props.contracts.walletAddress.substring(this.props.contracts.walletAddress.length - 4)}
                  </a>
                )}
              </div>
            </div>
          </div>
        </div>
      </nav>
    );
  }
}

export default NavBar;
