import React, { Component } from 'react';
import { EthereumService } from '../services/EthereumService';
import Content from './Content';
import NavBar from './NavBar';
import Message from './Message';
import { Subscription } from 'rxjs';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      opacity: 0.25,
      activeItem: '',
      message: '',
      error: '',
      contracts: {
        isLoaded: false,
        initError: false,
        walletAddress: '0x00',
        lastHarvestBlockNumber: 0,
        endOfGrowingCycle: 0,
        cornComposted: 0,
        ethBalance: 0,
        landBalance: 0,
        cornBalance: 0,
        cornToHarvest: 0,
        landInFarm: 0,
        globalLandInFarms: 0,
        globalNumberOfFarms: 0,
        farmMaturityBoost: 0,
        farmCompostBoost: 0,
        landSymbol: 'LAND',
        cornSymbol: 'CORN',
        transAmount: 0,
        isAuthorized: false,
        burnRequiredTo10x: 0,
        myRatioVsGlobalRatio: 0
      },
      claimInfo: {},
      prices: {}
    };

    this.subscription = new Subscription();
    this.ethereumService = new EthereumService();
    this.selectActiveItem = this.selectActiveItem.bind(this);
    this.showMessage = this.showMessage.bind(this);
    this.hideMessage = this.hideMessage.bind(this);
  }

  componentDidMount() {
    var isClaim = window.location.pathname.includes('claim');
    this.ethereumService.setShouldCheckClaim(isClaim);

    var eventSubscription = this.ethereumService.events.subscribe(
      event => {
        if (event) {
          this.showMessage(event);
        }
        this.setState({ contracts: this.ethereumService.contracts });
        this.setState({ prices: this.ethereumService.prices });
        this.setState({ claimInfo: this.ethereumService.claimService.claimInfo });
      },
      error => this.showMessage({ message: 'Unexpected error.', error: error })
    );

    this.subscription.add(eventSubscription);

    var errorSubscription = this.ethereumService.errors.subscribe(
      event => this.showMessage(event),
      error => this.showMessage({ message: 'Unexpected error.', error: error })
    );

    this.subscription.add(errorSubscription);
  }

  showMessage(event) {
    this.setState({
      message: event.message,
      messagePending: event.pending,
      error: event.error
    });
  }

  hideMessage() {
    this.setState({
      message: ''
    });
  }

  componentWillUnmount() {
    this.subscription.unsubscribe();
  }

  selectActiveItem(itemId) {
    this.setState({ activeItem: itemId });
  }

  render() {
    if (this.state.initError) {
      return (
        <div id="app">
          <section className="hero is-fullheight-with-navbar">
            <div className="hero-body">
              <div className="container">
                <h1 className="title">No Wallet</h1>
                <h2 className="subtitle">
                  <a href="https://metamask.io">Metamask</a> or equivalent required to access this page.
                </h2>
              </div>
            </div>
          </section>
        </div>
      );
    } else {
      return (
        <div id="app">
          <NavBar
            contracts={this.state.contracts}
            prices={this.state.prices}
            ethereumservice={this.ethereumService}
            activeItem={this.state.activeItem}
            onNavigated={this.selectActiveItem}
          />

          <Message
            message={this.state.message}
            hide={this.hideMessage}
            className={`${this.state.error ? 'is-danger' : this.state.messagePending ? 'is-warning' : 'is-success'}`}
          />

          <div className="hero is-fullheight-with-navbar">
            <div className="hero-head"></div>

            <Content
              contracts={this.state.contracts}
              prices={this.state.prices}
              claimInfo={this.state.claimInfo}
              ethereumservice={this.ethereumService}
              activeItem={this.state.activeItem}
              onNavigated={this.selectActiveItem}
            />

            <div className="hero-foot"></div>
          </div>
        </div>
      );
    }
  }
}

export default App;
