import React, { Component } from 'react';
import Farm from './Farm';
import Landing from './Landing';
import Docs from './Docs';
import Claim from './Claim';
import ErrorBoundary from './ErrorBoundary';
import { Switch, Route } from 'react-router-dom';

class Content extends Component {
  render() {
    return (
      <div id="content">
        <Switch>
          <Route exact path="/">
            <Landing contracts={this.props.contracts} />
          </Route>

          <Route path="/farm">
            <ErrorBoundary>
              <Farm
                ethereumservice={this.props.ethereumservice}
                contracts={this.props.contracts}
                prices={this.props.prices}
              />
            </ErrorBoundary>
          </Route>

          <Route path="/docs">
            <Docs ethereumservice={this.props.ethereumservice} contracts={this.props.contracts} />
          </Route>

          <Route path="/claim">
            <Claim ethereumservice={this.props.ethereumservice} claimInfo={this.props.claimInfo} />
          </Route>
        </Switch>
      </div>
    );
  }
}

export default Content;
